import { Component, OnInit, TemplateRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';

import { HttpService } from '../http.service';
import { Employee } from './models/employee';

@Component({
  selector: 'app-employee-details',
  templateUrl: './employee-details.component.html',
  styleUrls: ['./employee-details.component.css']
})
export class EmployeeDetailsComponent implements OnInit {

  id:number;

  empobj=<Employee>{};
  modalRef: BsModalRef;      
  config = {
   animated: true,

   ignoreBackdropClick: true,
   class: "alert alert-danger"
  };
 

  constructor(private route:ActivatedRoute,
    private service:HttpService,private modalservice:BsModalService
    ) { }

  ngOnInit() {
    this.GetAllDataFromURL();
  }

  GetAllDataFromURL(){
    this.route.paramMap
    .subscribe((param)=>{
     this.id=+param.get("id");
     console.log(this.id);

     this.GetParticularRecord(this.id)

    })

  }

 GetParticularRecord(id){
     this.service.getEMPByID(id)
     .subscribe((response)=>{
       console.log(response)
       this.empobj=(<Employee>response)
     })
}


OnUpdate(popup: TemplateRef<any>){
 
 this.modalRef=this.modalservice.show(popup,this.config)

}

}
